<script>
import {} from '../../config/api'
import { videoPlayer } from 'vue-video-player' //
import 'video.js/dist/video-js.css'
import introducePageTitle from './introducePageTitle.vue' //
import introducePageBottom from './introducePageBottom.vue' //
export default {
  data() {
    return {
      info1: '',
      info2: '',
      step: 2,
      dataArr: [{
        step: 2,
        info1: '',
        info2: '',
      }, {
        step: 1,
        info1: '',
        info2: 'HTTPS',
      }, {
        step: 0,
        info1: '',
        info2: '',
      }, {
        step: -1,
        info1: '',
        info2: '',
      }, ],


      searchBarFixed: false,
      showText1: false,
      showText2: false,
      showText3: false,

      showTitle1: false,
      showTitle2: false,
      showTitle3: false,
      showTitle4: false,
      showTitle5: false,

      androidInfo: {},
      iosInfo: {},
      company: {},
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //
        autoplay: false, //true,
        muted: false, // 
        loop: false, // 
        preload: 'auto', // <video>auto,
        language: 'zh-CN',
        aspectRatio: '16:9', //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [{
          type: "video/mp4",
          src: "https://saas-pewinner.oss-cn-hangzhou.aliyuncs.com/uploads/video/%E6%90%9C%20%E8%82%A1_%E6%97%A0%E6%B0%B4%E5%8D%B0.mp4" //url
        }],
        poster: "../../../static/images/introducePage_icon_50.png", //
        // width: document.documentElement.clientWidth,
        notSupportedMessage: '', //Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //
        }
      }
    }
  },
  components: { introducePageTitle, introducePageBottom, videoPlayer },
  created() {

  },
  mounted() {
    // window.addEventListener('scroll', this.handleScroll)
  },
  computed: {

  },
  methods: {
    toCut(step) {
      this.step = step;
      this.dataArr.forEach(element => {
        if (element.step == step) {
          this.info1 = element.info1;
          this.info2 = element.info2;
        }
      });
    },
    paperScroll(e) {
      let scrollTop = e.target.scrollTop;
      //let line2 = document.getElementsByClassName('line2')[0].offsetTop;
      // console.log(scrollTop)
      // console.log(line2);
      /*if(scrollTop > 455){
        this.searchBarFixed = true;
      }else{
        this.searchBarFixed = false;
      }*/
      if (scrollTop > 2100) {
        this.showText1 = true
      } else {
        this.showText1 = false
      }
      if (scrollTop > 2100) {
        this.showText2 = true
      } else {
        this.showText2 = false
      }
      if (scrollTop > 2100) {
        this.showText3 = true
      } else {
        this.showText3 = false
      }
      if (scrollTop > 600) {
        this.showTitle1 = true;
      } else {
        this.showTitle1 = false;
      }
      if (scrollTop > 1050) {
        this.showTitle2 = true;
      } else {
        this.showTitle2 = false;
      }
      if (scrollTop > 2000) {
        this.showTitle3 = true;
      } else {
        this.showTitle3 = false;
      }
    },

    toVipPage() {
      this.$router.push({
        path: '/vipPrice',
      })
    },
    toRegiste() {
      this.$router.push({
        path: '/register',
      })
    },
  }
}

